<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <!--工具条-->
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
                placeholder="监测对象名称、监测对象简称过滤"
            ></toolbar>
            <el-link
                type="primary"
                class="link"
                @click="uninputFormVisible = true"
                >待录入项目列表<font>{{ uninputCountStr }}</font></el-link
            >
            <!--列表-->
            <el-table
                :data="sites"
                v-loading="listLoading"
                :row-class-name="$tableRowClassName"
                style="width: 100%"
            >
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="sitename"
                    label="监测对象名称"
                    min-width="100"
                    show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column prop="siteshortname" label="监测对象简称">
                </el-table-column>
                <el-table-column prop="isgreen" label="绿色示范片区">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.isgreen == 1"
                            >绿色示范</el-tag
                        >
                        <el-tag type="info" v-else>非示范</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="status" label="状态">
                    <template slot-scope="scope">
                        <el-button
                            type="success"
                            size="small"
                            v-if="scope.row.status == 0"
                            >正常</el-button
                        >
                        <el-button
                            type="warning"
                            size="small"
                            v-else-if="scope.row.status == 1"
                            >冻结</el-button
                        >
                        <el-button
                            type="info"
                            size="small"
                            v-else-if="scope.row.status == 2"
                            >关闭</el-button
                        >
                    </template>
                </el-table-column>
                <el-table-column prop="status" label="信息">
                    <template slot-scope="scope">
                        <el-link
                            type="primary"
                            @click="showPic(scope.$index, scope.row)"
                            >图片</el-link
                        >
                        <el-link
                            type="primary"
                            @click="showSummary(scope.$index, scope.row)"
                            >简介</el-link
                        >
                    </template>
                </el-table-column>
                <el-table-column
                    label="操作"
                    min-width="200"
                    v-if="
                        canEdit ||
                        canStop ||
                        canRecover ||
                        canEquip ||
                        canBind ||
                        canComplete ||
                        canRemove ||
                        canUser
                    "
                >
                    <template slot-scope="scope">
                        <div v-if="scope.row.status < 2">
                            <el-tooltip
                                class="item"
                                effect="dark"
                                content="编辑"
                                placement="top"
                                :enterable="false"
                                :open-delay="500"
                            >
                                <el-button
                                    v-if="canEdit && scope.row.status < 2"
                                    plain
                                    type="warning"
                                    size="mini"
                                    @click="handleEdit(scope.$index, scope.row)"
                                    icon="el-icon-edit"
                                ></el-button>
                            </el-tooltip>

                            <el-tooltip
                                class="item"
                                effect="dark"
                                content="负责人"
                                placement="top"
                                :enterable="false"
                                :open-delay="500"
                            >
                                <el-button
                                    v-if="canUser && scope.row.status < 2"
                                    plain
                                    type="primary"
                                    size="mini"
                                    @click="handleUser(scope.$index, scope.row)"
                                    icon="el-icon-user"
                                ></el-button>
                            </el-tooltip>
                            <el-tooltip
                                class="item"
                                effect="dark"
                                content="暂停(可以恢复)"
                                placement="top"
                                :enterable="false"
                                :open-delay="500"
                                v-if="canStop && scope.row.status == 0"
                            >
                                <el-button
                                    plain
                                    type="info"
                                    size="mini"
                                    @click="
                                        handleStop(
                                            scope.$index,
                                            scope.row,
                                            $event
                                        )
                                    "
                                    icon="el-icon-video-pause"
                                ></el-button>
                            </el-tooltip>
                            <el-tooltip
                                class="item"
                                effect="dark"
                                content="恢复"
                                placement="top"
                                :enterable="false"
                                :open-delay="500"
                                v-else-if="canRecover && scope.row.status == 1"
                            >
                                <el-button
                                    plain
                                    type="success"
                                    size="mini"
                                    @click="
                                        handleRecover(
                                            scope.$index,
                                            scope.row,
                                            $event
                                        )
                                    "
                                    icon="el-icon-video-play"
                                ></el-button>
                            </el-tooltip>
                            <el-tooltip
                                class="item"
                                effect="dark"
                                content="平面图"
                                placement="top"
                                :enterable="false"
                                :open-delay="500"
                            >
                                <el-button
                                    v-if="canEquip && scope.row.status < 2"
                                    plain
                                    type="primary"
                                    size="mini"
                                    @click="
                                        handleEquip(scope.$index, scope.row)
                                    "
                                    icon="el-icon-map-location"
                                ></el-button>
                            </el-tooltip>

                            <el-tooltip
                                class="item"
                                effect="dark"
                                content="第三方绑定"
                                placement="top"
                                :enterable="false"
                                :open-delay="500"
                            >
                                <el-button
                                    v-if="canBind && scope.row.status < 2"
                                    plain
                                    type="success"
                                    size="mini"
                                    @click="handleBind(scope.$index, scope.row)"
                                    icon="fa fa-puzzle-piece"
                                ></el-button>
                            </el-tooltip>
                            <el-tooltip
                                class="item"
                                effect="dark"
                                content="关闭(停止接收数据，无法恢复)"
                                placement="top"
                                :enterable="false"
                                :open-delay="500"
                            >
                                <el-button
                                    v-if="canComplete && scope.row.status < 2"
                                    type="danger"
                                    size="mini"
                                    @click="
                                        handleComplete(
                                            scope.$index,
                                            scope.row,
                                            $event
                                        )
                                    "
                                    icon="el-icon-circle-close"
                                ></el-button>
                            </el-tooltip>
                        </div>
                        <div v-else>
                            <el-tooltip
                                class="item"
                                effect="dark"
                                content="移除(彻底移除，无法恢复)"
                                placement="top"
                                :enterable="false"
                                :open-delay="500"
                            >
                                <el-button
                                    v-if="canRemove && scope.row.status == 2"
                                    type="danger"
                                    size="mini"
                                    @click="
                                        handleRemove(
                                            scope.$index,
                                            scope.row,
                                            $event
                                        )
                                    "
                                    icon="el-icon-scissors"
                                ></el-button>
                            </el-tooltip>
                        </div>
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    layout="total, prev, pager, next, jumper"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>
        </el-main>
        <el-dialog
            title="简介"
            :visible.sync="summaryFormVisible"
            v-model="summaryForm"
            :close-on-click-modal="false"
        >
            <el-form
                label-position="top"
                label-width="80px"
                ref="summaryForm"
                :model="summaryForm"
            >
                <el-form-item>
                    <el-input
                        type="textarea"
                        rows="10"
                        maxlength="500"
                        v-model="summaryForm.summary"
                    ></el-input>
                </el-form-item>
                <input type="hidden" v-model="summaryForm.SITEID" />
                <input type="hidden" v-model="summaryForm.GROUPID" />
                <el-form-item class="form-button">
                    <el-button
                        type="primary"
                        @click="saveSiteSummary"
                        :loading="summaryLoading"
                        >保存</el-button
                    >
                    <el-button @click="closeSiteSummary">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog
            title="图片"
            :visible.sync="imgModalVisible"
            :close-on-click-modal="false"
            @close="dialogClose"
        >
            <el-form
                label-position="top"
                label-width="150px"
                ref="siteImgForm"
                :model="siteImgForm"
            >
                <el-form-item label="项目建成后效果图" prop="rendering">
                    <el-upload
                        ref="upload"
                        action="1"
                        :limit="10"
                        :on-remove="handleRemove1"
                        accept=".jpg"
                        :auto-upload="true"
                        :http-request="fileUpload1"
                        :on-change="imgFileChanged1"
                        list-type="picture-card"
                        :file-list="siteImgForm.rendering"
                    >
                        <i slot="default" class="el-icon-plus"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label="扬尘公示牌及夜间施工许可" prop="dust">
                    <el-upload
                        ref="upload2"
                        action="1"
                        :limit="10"
                        :on-remove="handleRemove2"
                        accept=".jpg"
                        :auto-upload="true"
                        :http-request="fileUpload2"
                        :on-change="imgFileChanged2"
                        list-type="picture-card"
                        :file-list="siteImgForm.dust"
                    >
                        <i slot="default" class="el-icon-plus"></i>
                    </el-upload>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="imgModalVisible = false"
                    >取消</el-button
                >
                <el-button
                    type="primary"
                    @click.native="addPicSubmit"
                    :loading="siteImgFormLoading"
                    >提交</el-button
                >
            </div>
        </el-dialog>
        <el-dialog
            :title="`【${currsiteshortname}】第三方绑定`"
            :visible.sync="bindFormVisible"
            :close-on-click-modal="false"
        >
            <el-form
                label-position="top"
                label-width="80px"
                ref="bindForm"
                :model="bindForm"
            >
                <table class="layout-table">
                    <tr>
                        <th>实名制</th>
                        <td>
                            <el-form-item style="width: 100px; float: left">
                                <el-select
                                    v-model="bindForm.attendprojtype"
                                    placeholder="无绑定"
                                    @change="attendTypeChange"
                                >
                                    <el-option
                                        v-for="item in attendtypes"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id"
                                    ></el-option> </el-select
                            ></el-form-item>
                            <el-form-item
                                style="width: calc(100% - 100px); float: left"
                            >
                                <el-select
                                    v-model="bindForm.attendprojid"
                                    placeholder="无绑定"
                                    v-if="bindForm.attendprojtype != 4"
                                    :filterable="true"
                                >
                                    <el-option
                                        v-for="item in attendprojids"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id"
                                    ></el-option>
                                </el-select>
                                <el-input
                                    type="text"
                                    maxlength="50"
                                    v-model="bindForm.attendprojid"
                                    placeholder="无绑定"
                                    v-else
                                ></el-input>
                            </el-form-item>
                        </td>
                        <th>车辆冲洗抓拍</th>
                        <td>
                            <el-form-item>
                                <el-input
                                    type="text"
                                    maxlength="50"
                                    v-model="bindForm.parkkey"
                                    placeholder="无绑定"
                                ></el-input>
                            </el-form-item>
                        </td>
                    </tr>
                    <tr>
                        <th>人员立体定位</th>
                        <td>
                            <el-form-item style="width: 50%; float: left">
                                <el-input
                                    type="text"
                                    maxlength="25"
                                    v-model="bindForm.szhelmetname"
                                    placeholder="无账号"
                                ></el-input>
                            </el-form-item>
                            <el-form-item style="width: 50%; float: left">
                                <el-input
                                    type="text"
                                    maxlength="25"
                                    v-model="bindForm.szhelmetpwd"
                                    placeholder="无密码"
                                ></el-input>
                            </el-form-item>
                        </td>
                        <th>未戴安全帽</th>
                        <td>
                            <el-form-item>
                                <el-input
                                    type="text"
                                    maxlength="50"
                                    v-model="bindForm.helmetprojid"
                                    placeholder="无绑定"
                                ></el-input>
                            </el-form-item>
                        </td>
                    </tr>

                    <tr>
                        <th>陌生人识别</th>
                        <td>
                            <el-form-item>
                                <el-input
                                    type="text"
                                    maxlength="50"
                                    v-model="bindForm.strangerprojid"
                                    placeholder="无绑定"
                                ></el-input>
                            </el-form-item>
                        </td>
                        <th>人车分流</th>
                        <td>
                            <el-form-item>
                                <el-input
                                    type="text"
                                    maxlength="50"
                                    v-model="bindForm.trespasserprojid"
                                    placeholder="无绑定"
                                ></el-input>
                            </el-form-item>
                        </td>
                    </tr>
                    <tr>
                        <th>堆场火灾</th>
                        <td>
                            <el-form-item>
                                <el-input
                                    type="text"
                                    maxlength="50"
                                    v-model="bindForm.fireprojid"
                                    placeholder="无绑定"
                                ></el-input>
                            </el-form-item>
                        </td>
                        <th>升降机超员</th>
                        <td>
                            <el-form-item>
                                <el-input
                                    type="text"
                                    maxlength="50"
                                    v-model="bindForm.liftoverprojid"
                                    placeholder="无绑定"
                                ></el-input>
                            </el-form-item>
                        </td>
                    </tr>
                    <tr>
                        <th>晨会交底</th>
                        <td>
                            <el-form-item>
                                <el-input
                                    type="text"
                                    maxlength="50"
                                    v-model="bindForm.amdiscloseprojid"
                                    placeholder="无绑定"
                                ></el-input>
                            </el-form-item>
                        </td>
                        <th>密闭运输</th>
                        <td>
                            <el-form-item>
                                <el-input
                                    type="text"
                                    maxlength="50"
                                    v-model="bindForm.airtightprojid"
                                    placeholder="无绑定"
                                ></el-input>
                            </el-form-item>
                        </td>
                    </tr>
                    <tr>
                        <th>围挡喷淋</th>
                        <td>
                            <el-form-item>
                                <el-input
                                    type="text"
                                    maxlength="50"
                                    v-model="bindForm.sprayprojid"
                                    placeholder="无绑定"
                                ></el-input>
                            </el-form-item>
                        </td>
                        <th>裸土覆盖</th>
                        <td>
                            <el-form-item>
                                <el-input
                                    type="text"
                                    maxlength="50"
                                    v-model="bindForm.soilprojid"
                                    placeholder="无绑定"
                                ></el-input>
                            </el-form-item>
                        </td>
                    </tr>
                    <!-- <tr>
                        <th>反光背心</th>
                        <td>
                            <el-form-item>
                                <el-input
                                    type="text"
                                    maxlength="50"
                                    v-model="bindForm.vestprojid"
                                    placeholder="无绑定"
                                ></el-input>
                            </el-form-item>
                        </td>
                        <th></th>
                        <td></td>
                    </tr> -->
                </table>

                <input type="hidden" v-model="bindForm.SITEID" />
                <el-form-item class="form-button">
                    <el-button @click="closeSiteBind">取消</el-button>
                    <el-button
                        type="primary"
                        @click="saveSiteBind"
                        :loading="bindLoading"
                        >提交</el-button
                    >
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog
            title="待录入项目"
            :visible.sync="uninputFormVisible"
            :close-on-click-modal="true"
            top="5vh"
        >
            <el-table
                :data="uninputSites"
                v-loading="uninputLoading"
                :row-class-name="$tableRowClassName"
                style="width: 100%"
            >
                <el-table-column type="index" width="60">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="recordNumber"
                    label="安监备案号"
                    min-width="1"
                    show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                    prop="projectName"
                    min-width="2"
                    label="监测对象名称"
                >
                </el-table-column>
                <el-table-column prop="sgdw" min-width="2" label="施工单位">
                </el-table-column>
            </el-table>
        </el-dialog>
    </el-container>
</template>
<script>
import {
    getSitePage,
    getGroupSiteCount,
    getsite,
    stopSite,
    recoverSite,
    completeSite,
    removeSite,
    getSiteSummary,
    saveSiteSummary,
    getAttendProjList,
    saveSiteBind,
    postfile,
    setsitepic,
    getSitePics,
    getSiteUninput,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import util from "../../../util/date";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
export default {
    components: { leftgroup, Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            buttonList: [],
            sites: [],
            groupCounts: [],
            groups: [],
            listLoading: false,
            page: 1,
            pageSize: 10,
            total: 0,
            selectGroupId: 0,
            canEdit: false,
            canStop: false,
            canEquip: false,
            canBind: false,
            canComplete: false,
            canRemove: false,
            canRecover: false,
            canUser: false,
            summaryFormVisible: false,
            summaryForm: {},
            summaryLoading: false,
            imgModalVisible: false,
            siteImgFormLoading: false,
            siteImgForm: {
                rendering: [],
                dust: [],
                // rendering:[],
                // dust:[],
                // renderingOrigin:[],
                // dustOrigin:[],
                renderingDel: [],
                dustDel: [],
            },
            showpicrow: {},
            bindFormVisible: false,
            bindLoading: false,
            currsiteshortname: "",
            bindForm: {},
            attendtypes: [
                { id: "0", name: "无绑定" },
                { id: "5", name: "新合盛" },
                { id: "1", name: "群耀" },
                { id: "4", name: "2.0" },
                { id: "2", name: "大运" },
                // { id: "3", name: "魏凯" },
            ],
            attendprojids: [],
            attendprojidsall: {},
            currattendprojtype: 0,
            currattendprojid: 0,
            uninputSites: [],
            uninputLoading: false,
            uninputFormVisible: false,
            uninputCountStr: "(0件)",
        };
    },
    watch: {
        $route(to, from) {
            if (from.path.indexOf("/site/edit/") == 0) {
                this.getGroupCount();
                this.getSitePage();
            }
        },
    },
    methods: {
        addPicSubmit: function () {
            let _this = this;

            //this.$confirm("确认提交吗？", "提示", {}).then(() => {
            this.siteImgFormLoading = true;

            let { imgurl, ...para } = Object.assign(
                { SITEID: this.showpicrow.SITEID },
                this.siteImgForm
            );

            setsitepic(para).then((res) => {
                if (res.success) {
                    this.$message({
                        message: "设置成功",
                        type: "success",
                    });
                    this.$refs["siteImgForm"].resetFields();
                    this.imgModalVisible = false;
                    //this.getPageList();
                    //this.getGroupCounts();
                } else {
                    this.$message({
                        message: res.msg,
                        type: "error",
                    });
                }
                this.siteImgFormLoading = false;
            });
            //});
        },
        dialogClose() {
            this.$refs["siteImgForm"].resetFields();
        },
        imgFileChanged1(file, fileList) {
            this.siteImgForm.rendering = fileList;
        },
        imgFileChanged2(file, fileList) {
            this.siteImgForm.dust = fileList;
        },
        handleRemove1(file, fileList) {
            this.siteImgForm.rendering = fileList;
            this.siteImgForm.renderingDel.push(file.url);
        },
        handleRemove2(file, fileList) {
            this.siteImgForm.dust = fileList;
            this.siteImgForm.dustDel.push(file.url);
        },
        fileUpload1(param) {
            let fileObj = param.file; // 文件对象
            let form = new FormData(); // FormData 对象
            form.append("formFiles", fileObj);
            postfile(form)
                .then((res) => {
                    this.siteImgForm.rendering[
                        this.siteImgForm.rendering.length - 1
                    ].filename = res.data;
                })
                .catch((err) => {});
        },
        fileUpload2(param) {
            let fileObj = param.file; // 文件对象
            let form = new FormData(); // FormData 对象
            form.append("formFiles", fileObj);
            postfile(form)
                .then((res) => {
                    this.siteImgForm.dust[
                        this.siteImgForm.dust.length - 1
                    ].filename = res.data;
                })
                .catch((err) => {});
        },
        callFunction(item) {
            this.filters = item.search;
            this[item.Func].apply(this, item);
        },
        handleCurrentChange(val) {
            this.getSitePage(val);
        },
        getSitePage(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            this.listLoading = true;
            let paras = {
                groupid: this.selectGroupId,
                type: 1,
                keyword: this.filters,
                page: this.page,
                size: this.pageSize,
            };
            getSitePage(paras).then((res) => {
                if (res.success) {
                    this.sites = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        getGroupCount() {
            getGroupSiteCount().then((res) => {
                this.groupCounts = res.data;
            });
        },
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getSitePage();
        },
        //停用
        handleStop(index, r, e) {
            let target = e.target.parentNode;
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要停用的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认暂停该项目吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    stopSite(row.SITEID).then((res) => {
                        if (util.isEmt.format(res)) {
                            this.listLoading = false;
                            return;
                        }
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "暂停成功",
                                type: "success",
                            });
                            this.clearGroupSites();
                        } else {
                            console.log("暂停失败 " + res.msg);
                            this.$message({
                                message: "暂停失败",
                                type: "error",
                            });
                        }

                        this.getSitePage(this.page);
                        target.blur();
                    });
                })
                .catch(() => {});
        },
        //恢复
        handleRecover(index, r, e) {
            let target = e.target.parentNode;
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要恢复的一行数据！",
                    type: "error",
                });

                return;
            }
            this.listLoading = true;
            recoverSite(row.SITEID).then((res) => {
                if (util.isEmt.format(res)) {
                    this.listLoading = false;
                    return;
                }
                this.listLoading = false;
                if (res.success) {
                    this.$message({
                        message: "恢复成功",
                        type: "success",
                    });

                    this.clearGroupSites();
                } else {
                    console.log("恢复失败 " + res.msg);
                    this.$message({
                        message: "恢复失败",
                        type: "error",
                    });
                }

                this.getSitePage(this.page);
                target.blur();
            });
        },
        //关闭
        handleComplete(index, r, e) {
            let target = e.target.parentNode;
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要关闭的一行数据！",
                    type: "error",
                });

                return;
            }

            this.$confirm(
                "关闭以后停止接收所有数据，无法恢复，确认关闭该项目吗?",
                "提示",
                {
                    type: "warning",
                }
            ).then(() => {
                this.listLoading = true;
                completeSite(row.SITEID).then((res) => {
                    if (util.isEmt.format(res)) {
                        this.listLoading = false;
                        return;
                    }
                    this.listLoading = false;
                    if (res.success) {
                        this.$message({
                            message: "关闭成功",
                            type: "success",
                        });
                        this.clearGroupSites();
                    } else {
                        console.log("关闭失败 " + res.msg);
                        this.$message({
                            message: "关闭失败",
                            type: "error",
                        });
                    }

                    this.getSitePage(this.page);
                    target.blur();
                });
            });
        },
        //移除
        handleRemove(index, r, e) {
            let target = e.target.parentNode;
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要移除的一行数据！",
                    type: "error",
                });
                return;
            }
            this.$confirm(
                "移除以后无法恢复，数据无法查看，确认移除该项目吗?",
                "提示",
                {
                    type: "warning",
                }
            ).then(() => {
                this.listLoading = true;
                removeSite(row.SITEID).then((res) => {
                    if (util.isEmt.format(res)) {
                        this.listLoading = false;
                        return;
                    }
                    this.listLoading = false;
                    if (res.success) {
                        this.$message({
                            message: "移除成功",
                            type: "success",
                        });
                        this.clearGroupSites();
                    } else {
                        console.log("移除失败 " + res.msg);
                        this.$message({
                            message: "移除失败",
                            type: "error",
                        });
                    }

                    this.getSitePage(this.page);
                    target.blur();
                });
            });
        },
        async handleEdit(index, r) {
            this.$router.replace(`/site/edit/${r.SITEID}`);
        },
        async handleEquip(index, r) {
            this.$router.replace(`/site/equip/${r.SITEID}`);
        },
        async handleUser(index, r) {
            this.$router.replace(`/site/user/${r.SITEID}`);
        },
        async handleBind(index, r) {
            this.currsiteshortname = r.siteshortname;
            this.selectGroupId = r.GROUPID;
            this.getSite(r.SITEID);
            this.bindFormVisible = true;
        },
        handleAdd() {
            this.$router.replace(`/site/edit/0`);
        },
        showPic(index, row) {
            this.imgModalVisible = true;
            this.showpicrow = row;
            this.siteImgForm.rendering = [];
            this.siteImgForm.dust = [];
            this.siteImgForm.renderingDel = [];
            this.siteImgForm.dustDel = [];

            let param = { siteid: row.SITEID };

            getSitePics(param).then((res) => {
                if (res.success) {
                    if (res.data) {
                        res.data.forEach((e) => {
                            if (e.filetype == 1) {
                                this.siteImgForm.rendering.push(e);
                            } else if (e.filetype == 2) {
                                this.siteImgForm.dust.push(e);
                            }
                        });
                    }
                }
            });
        },
        showSummary(index, row) {
            this.summaryLoading = true;
            let paras = {
                groupid: row.GROUPID,
                siteid: row.SITEID,
            };
            getSiteSummary(paras).then((res) => {
                if (res.success) {
                    if (res.data) {
                        this.summaryForm = res.data;
                    } else {
                        this.summaryForm.groupid = row.GROUPID;
                        this.summaryForm.siteid = row.SITEID;
                    }
                    this.summaryFormVisible = true;
                }
                this.summaryLoading = false;
            });
        },
        closeSiteSummary() {
            this.summaryForm = {};
            this.summaryFormVisible = false;
        },
        closeSiteBind() {
            this.bindForm = {};
            this.bindFormVisible = false;
        },
        saveSiteSummary() {
            this.$refs.summaryForm.validate((valid) => {
                if (valid) {
                    this.summaryLoading = true;
                    let para = Object.assign({}, this.summaryForm);
                    saveSiteSummary(para).then((res) => {
                        if (res.success && res.data > 0) {
                            this.summaryFormVisible = false;
                            this.$message({
                                message: "保存成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.summaryLoading = false;
                    });
                }
            });
        },
        saveSiteBind() {
            this.$refs.bindForm.validate((valid) => {
                if (valid) {
                    this.bindLoading = true;
                    let para = Object.assign({}, this.bindForm);
                    let szhelmetnamepwd = "";

                    if (para.szhelmetname && para.szhelmetpwd) {
                        szhelmetnamepwd =
                            para.szhelmetname + "::" + para.szhelmetpwd;
                    }
                    para.szhelmetnamepwd = szhelmetnamepwd;
                    saveSiteBind(para).then((res) => {
                        if (res.success && res.data > 0) {
                            this.bindFormVisible = false;
                            this.$message({
                                message: "保存成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.bindLoading = false;
                    });
                }
            });
        },
        getSite(siteid) {
            getsite({ id: siteid }).then((res) => {
                if (res.success) {
                    let szhelmetnamepwd = res.data.szhelmetnamepwd;
                    let szhelmetname = "";
                    let szhelmetpwd = "";
                    if (szhelmetnamepwd && szhelmetnamepwd.indexOf("::") > 0) {
                        szhelmetname = szhelmetnamepwd.split("::")[0];
                        szhelmetpwd = szhelmetnamepwd.split("::")[1];
                    }
                    res.data.szhelmetname = szhelmetname;
                    res.data.szhelmetpwd = szhelmetpwd;
                    this.currattendprojtype = res.data.attendprojtype;
                    this.currattendprojid = res.data.attendprojid;
                    this.bindForm = res.data;
                    this.attendTypeChange(res.data.attendprojtype);
                }
            });
        },
        attendTypeChange(val) {
            if (val == this.currattendprojtype) {
                this.bindForm.attendprojid = this.currattendprojid;
            } else {
                this.bindForm.attendprojid = "";
            }

            if (val == 0) {
                this.attendprojids = [];
                return;
            }
            let groupid = this.selectGroupId.toString();
            if (!this.attendprojidsall[groupid]) {
                this.attendprojidsall[groupid] = {};
            }
            if (!this.attendprojidsall[groupid][val.toString()]) {
                let paras = {
                    groupid: this.selectGroupId,
                    type: val,
                };
                getAttendProjList(paras).then((res) => {
                    if (res.success) {
                        this.attendprojidsall[groupid][val.toString()] =
                            res.data.map((item) => {
                                let result = {};
                                if (val == 1) {
                                    //群耀
                                    result.id = item.PrjNum;
                                    result.name = item.xmmc;
                                } else if (val == 2) {
                                    //大运
                                    result.id = item.itemId;
                                    result.name = item.itemName;
                                } else if (val == 5) {
                                    //新合盛
                                    result.id = item.projectCode;
                                    result.name = item.projectName;
                                }
                                return result;
                            });
                        this.attendprojids =
                            this.attendprojidsall[groupid][val.toString()];
                        if (val == this.currattendprojtype) {
                            this.bindForm.attendprojid = this.currattendprojid;
                        }
                    } else {
                        this.attendprojids = [];
                    }
                });
            } else {
                this.attendprojids =
                    this.attendprojidsall[groupid][val.toString()];
                if (val == this.currattendprojtype) {
                    this.bindForm.attendprojid = this.currattendprojid;
                }
            }
        },
        getUninput() {
            getSiteUninput().then((res) => {
                this.uninputLoading = true;
                if (res.success) {
                    if (res.data) {
                        this.uninputSites = res.data;
                        this.uninputCountStr = `(${this.uninputSites.length}件)`;
                    } else {
                        this.uninputSites = [];
                        this.uninputCountStr = "(0件)";
                    }
                }
                this.uninputLoading = false;
            });
        },
    },
    mounted() {
        this.getUninput();
        this.getGroupCount();
        this.getSitePage();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);

        buttons.forEach((element) => {
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleStop") {
                this.canStop = true;
            } else if (element.Func == "handleEquip") {
                this.canEquip = true;
            } else if (element.Func == "handleBind") {
                this.canBind = true;
            } else if (element.Func == "handleComplete") {
                this.canComplete = true;
            } else if (element.Func == "handleRemove") {
                this.canRemove = true;
            } else if (element.Func == "handleRecover") {
                this.canRecover = true;
            } else if (element.Func == "handleUser") {
                this.canUser = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.el-select {
    width: 100%;
}
.el-date-editor.el-input,
.el-date-editor.el-input__inner,
.el-cascader {
    width: 100%;
}
.bm-view {
    padding-left: 10px;
    width: 100%;
    height: 600px;
}
.layout-table {
    width: 100%;
    vertical-align: middle;
    border-collapse: collapse;
    table-layout: fixed;
    margin-bottom: 20px;
}
.layout-table th {
    font-weight: 100;
    background: rgb(240, 240, 240);
    border: 1px solid #e8eaf1;
    width: 100px;
}
.layout-table td {
    border: 1px solid #e8eaf1;
}
.el-form-item {
    margin-bottom: 0;
}
.el-button--mini {
    font-size: 16px;
    font-weight: 900;
}
.fa.fa-puzzle-piece {
    margin-right: 0 !important;
    color: red;
}
.link {
    position: absolute;
    right: 40px;
    top: 20px;
}
</style>


